$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}CartList {
    position: relative;

    height: 792px;
    box-sizing: border-box;


    display: flex;
    flex-direction: column;
    justify-content: center;

    &__container {
        position: relative;

        width: 832px;
        max-height: 608px;

        overflow: hidden;

        --spatial-navigation-contain: contain;
    }

    &__wrapper {
        overflow-x: hidden;

        width: 832px;
        padding-right: 10px;

        will-change: transform;
        transition: transform .5s ease-in-out;
    }

    &__total-price {
        margin-top: 32px;
        margin-left: 16px;
        line-height: 56px;
        font-size: 48px;
        font-weight: 600;
        color: #fff;
    }

    &__minimal-price {
        margin: 8px 0 0 16px;
        font-weight: 500;
        font-size: 32px;
        line-height: 40px;
        color: #F6650A;
    }

    &__gradient {
        position: absolute;
        left: 0;
        right: 0;

        height: 160px;

        z-index: 1;

        background: linear-gradient(0, #001124 0%, rgba(0, 17, 36, 0) 89.09%);

        &_top {
            top: -1px;

            transform: rotate(180deg);
        }

        &_bottom {
            bottom: -1px;
        }

        &_hidden {
            opacity: 0;
        }
    }
}
