$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}CartInfo {
    position: relative;
    display: flex;

    padding-top: 48px;
    box-sizing: border-box;

    flex-direction: column;
    align-items: center;

    width: 776px;

    &__badge {
        display: flex;
        flex-direction: column;

        align-items: center;

        width: 132px;
        height: 124px;
    }

    &__badge-body {
        position: relative;

        width: 132px;
        height: 86px;

        background: #E12D39;
        border-radius: 108px;

        display: flex;

        flex-direction: column;

        align-items: center;
        justify-content: center;
    }

    &__badge-name {
        color: #fff;
        height: 34px;
        font-size: 32px;
        font-weight: 600;
        line-height: 34px;
        text-align: center;
    }

    &__badge-desc {
        height: 20px;
        color: rgba(255, 255, 255, 0.8);
        font-size: 24px;
        font-weight: 500;
        line-height: 20px;
        text-align: center;
    }

    &__badge-kernel {
        //position: absolute;
        //top: 86px;
        height: 34px;
        width: 4px;

        background: #E12D39;
        border-bottom-left-radius: 2px;
        border-bottom-right-radius: 2px;
    }

    &__badge-shadow_big {
        margin-top: -2px;

        width: 74px;
        height: 6px;
        background: #06182E;
        filter: blur(6px);

        display: flex;
        justify-content: center;
    }

    &__badge-shadow_small {
        margin-top: -4px;
        width: 50px;
        height: 4px;
        background: #06182E;
        filter: blur(4px);
    }

    &__address {
        margin-top: 48px;

        display: flex;
        flex-direction: column;
        align-items: center;

        height: 56px;
        font-weight: 600;
        font-size: 48px;
        line-height: 56px;
        color: #fff;
    }

    &__personal {
        margin-top: 8px;

        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__personal-location {
        margin-top: 8px;
        height: 40px;
        font-size: 32px;
        font-weight: 500;
        line-height: 40px;
        color: #fff;
    }

    &__personal-phone-number {
        margin-top: 60px;
        height: 56px;
        font-weight: 600;
        font-size: 48px;
        line-height: 56px;
        color: #fff;
    }

    &__personal-name {
        margin-top: 8px;
        height: 40px;
        font-size: 32px;
        font-weight: 500;
        line-height: 40px;
        color: #fff;
    }

    &__ask-personal {
        margin-top: 14px;
        text-align: center;

        font-size: 32px;
        line-height: 44px;
        color: rgba(255, 255, 255, 0.74);
    }

    &__button {
        margin-top: 64px;

        padding: 20px 40px;
        border-radius: 40px;

        display: flex;

        border: none;

        color: #fff;

        font-weight: 600;
        font-size: 32px;

        align-items: center;
        justify-content: center;

        background-color: rgba(255, 255, 255, .1);

        transition: background-color .3s ease-in-out;

        &:focus:not(&_disabled) {
            background-color: #08A652;
        }
    }
}
