$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}CartListItem {
    position: relative;

    display: flex;

    height: 152px;
    width: 832px;

    align-items: center;

    padding: 16px;
    box-sizing: border-box;

    &_sold-out {
        .#{$ns}CartListItem {
            &__index-badge,
            &__image,
            &__name,
            &__price {
                opacity: .24;
            }

            &__price {
                color: #fff;
            }
        }
    }

    &__index-badge {
        position: absolute;
        left: 8px;
        top: 8px;

        display: flex;
        align-items: center;
        justify-content: center;

        height: 64px;
        width: 64px;

        font-size: 28px;
        font-weight: 600;

        border-radius: 50%;

        color: rgba(0, 0, 0, 0.55);

        background-color: #EAEAEC;
    }

    &__image {
        height: 120px;
        width: 120px;
        border-radius: 16px;
        margin-right: 32px;

        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
    }

    &__info {
        width: 384px;
        margin-right: 48px;

        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__name {
        line-height: 40px;
        font-size: 32px;
        font-weight: 500;
        color: #fff;

        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;

        &_one-line {
            -webkit-line-clamp: 1;
        }
    }

    &__price {
        margin-top: 4px;
        height: 36px;
        line-height: 36px;
        font-size: 28px;
        color: rgba(255, 255, 255, 0.47);
    }

    &__sold-out {
        margin-top: 4px;
        font-weight: 500;
        font-size: 28px;
        line-height: 36px;
        color: #F6650A;
    }

    &__selector {
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 8px;
        box-sizing: border-box;

        width: 216px;
        height: 80px;
        border-radius: 40px;

        will-change: background-color;

        transition: .2s ease-in-out;

        &_selected {
            background: rgba(255, 255, 255, 0.05);
        }
    }

    &__selector-value {
        font-size: 32px;
        line-height: 40px;
        font-weight: 600;
        color: #fff;
    }

    & &__remove-button {
        background: rgba(255, 255, 255, 0.05);

        &:focus {
            background-color: #E12D39;
        }
    }

    &__selector-button {
        display: flex;
        align-items: center;
        justify-content: center;

        background: rgba(255, 255, 255, 0.05);
        border-radius: 32px;

        height: 64px;
        width: 64px;

        border: none;

        will-change: background-color;

        transition: .2s ease-in-out;

        &::before {
            content: '';

            height: 24px;
            width: 24px;

            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
        }

        &[disabled] {
            opacity: .4;
        }


        &_less {
            &:focus {
                background: #08A652;
            }

            &::before {
                background-image: url('../../../assets/icons/minus.svg');
            }
        }

        &_more {
            &:focus {
                background: #08A652;
            }

            &::before {
                background-image: url('../../../assets/icons/plus.svg');
            }
        }

        &_remove {
            transform: rotate(45deg);

            &:focus {
                background-color: #E12D39;
            }

            &::before {
                background-image: url('../../../assets/icons/plus.svg');
            }
        }
    }
}
